    transform: scale(1.1);
  }
  100% {
    opacity: 0.4; /* ìµœì†Œ íˆ¬ëª…ë„ë¥¼ 0.4ë¡œ ì¡°ì • (ë” ë°ê²Œ) */
    transform: scale(0.7);
  }
}

