* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', 'Noto Sans KR', sans-serif;
  color: #ffffff;
  background: #000;
  overflow-x: hidden;
  
}

/* ë©”ì¸ ì•± ë˜í¼ */
.app {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
}

/* í•µì‹¬ ì•± ì»¨í…Œì´ë„ˆ */
.app-container {
  width: 100vw;
  height: 100vh;
